import React, { useEffect, useState } from 'react';
import axios from 'axios';
import {
  PlayCircle,
  CheckCircle2,
  Lock,
  FileText,
  Video,
  Clock,
  Calendar,
  BookOpen,
  BarChart3,
  MessageSquare,
  MessagesSquare,
  ExternalLink,
  X,
  Download,
  ChevronDown,
  ChevronUp,
  Settings,
  LogOut,
  User,
  HelpCircle,
  Bell
} from 'lucide-react';

// Interfaces
interface UserData {
  nombre?: string;
  apellido?: string;
  email?: string;
  id?: number;
}

interface Material {
  id: number;
  titulo: string;
  link_drive: string;
  tipo: 'tema' | 'bibliografia' | 'recurso';
}

interface Lesson {
  id: number;
  title: string;
  description: string;
  duration: string;
  type: 'video';
  hasResources: boolean;
  date: string;
  date_raw?: string; // Para guardar la fecha en formato raw para comparaciones
  completed?: boolean;
  locked?: boolean;
  link_zoom?: string;
  materiales?: Material[];
}

interface Module {
  id: number;
  moduleNumber?: number;
  title: string;
  description: string;
  duration: string;
  progress: number;
  completed?: boolean;
  locked?: boolean;
  lessons: Lesson[];
}

interface Course {
  id: number;
  titulo: string;
  descripcion: string;
  activo: boolean;
  duracion_total: string;
  Tutor?: string;
  ProfesionTutor?: string;
  modules: Module[];
  modulos?: Module[];
}

interface Capsula {
  id: number;
  titulo: string;
  descripcion: string;
  link_drive: string;
}

// Configuracin de la URL de API
const API_URL = process.env.BACK_URL || 'http://localhost:1337';

// Configuracin de Axios
const api = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Funcin para obtener el token JWT del localStorage
const getToken = () => {
  return localStorage.getItem('userToken');
};


api.interceptors.request.use(
  (config) => {
    const token = getToken();
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Modal de materiales
interface MaterialModalProps {
  isOpen: boolean;
  onClose: () => void;
  materials: Material[];
  lessonTitle: string;
}

const MaterialModal: React.FC<MaterialModalProps> = ({ isOpen, onClose, materials, lessonTitle }) => {
  if (!isOpen) return null;

  const getMaterialTypeIcon = (tipo: string) => {
    switch (tipo) {
      case 'tema':
        return <FileText className="h-5 w-5 text-blue-400" />;
      case 'bibliografia':
        return <BookOpen className="h-5 w-5 text-green-400" />;
      case 'recurso':
      default:
        return <Download className="h-5 w-5 text-purple-400" />;
    }
  };

  const getMaterialTypeText = (tipo: string) => {
    switch (tipo) {
      case 'tema':
        return 'Material de clase';
      case 'bibliografia':
        return 'Bibliografa';
      case 'recurso':
      default:
        return 'Recurso adicional';
    }
  };

  return (
    <div className="fixed inset-0 bg-black/70 backdrop-blur-sm flex items-center justify-center z-50 p-4">
      <div className="bg-[#0d1526] border border-gray-700 rounded-xl max-w-2xl w-full max-h-[80vh] overflow-hidden flex flex-col">
        <div className="p-6 border-b border-gray-700 flex justify-between items-center">
          <h3 className="text-xl font-semibold text-white">Materiales: {lessonTitle}</h3>
          <button 
            onClick={onClose}
            className="text-gray-400 hover:text-white transition-colors"
          >
            <X className="h-6 w-6" />
          </button>
        </div>
        
        <div className="p-6 overflow-y-auto flex-1">
          {materials.length === 0 ? (
            <p className="text-gray-400">No hay materiales disponibles para esta clase.</p>
          ) : (
            <div className="space-y-4">
              {materials.map((material) => (
                <div key={material.id} className="bg-gray-800/70 rounded-lg p-4 flex items-center justify-between">
                  <div className="flex items-center space-x-4">
                    <div className="p-2 bg-gray-700 rounded-lg">
                      {getMaterialTypeIcon(material.tipo)}
                    </div>
                    <div>
                      <h4 className="text-white font-medium">{material.titulo}</h4>
                      <p className="text-sm text-gray-300">{getMaterialTypeText(material.tipo)}</p>
                    </div>
                  </div>
                  <a 
                    href={material.link_drive} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className="px-4 py-2 bg-blue-600/20 hover:bg-blue-600/30 text-blue-300 rounded-lg transition-colors flex items-center space-x-2"
                  >
                    <ExternalLink className="h-4 w-4" />
                    <span>Abrir</span>
                  </a>
                </div>
              ))}
            </div>
          )}
        </div>
        
        <div className="p-4 border-t border-gray-700 bg-gray-900/80">
          <button
            onClick={onClose}
            className="w-full py-2 bg-gray-700 hover:bg-gray-600 text-white rounded-lg transition-colors"
          >
            Cerrar
          </button>
        </div>
      </div>
    </div>
  );
};

// Modal de cpsulas
interface CapsulasModalProps {
  isOpen: boolean;
  onClose: () => void;
  capsulas: Capsula[];
  loading: boolean;
  error: string | null;
}

const CapsulasModal: React.FC<CapsulasModalProps> = ({ isOpen, onClose, capsulas, loading, error }) => {
  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black/70 backdrop-blur-sm flex items-center justify-center z-50 p-4">
      <div className="bg-[#0d1526] border border-gray-700 rounded-xl max-w-4xl w-full max-h-[80vh] overflow-hidden flex flex-col">
        <div className="p-6 border-b border-gray-700 flex justify-between items-center">
          <h3 className="text-xl font-semibold text-white">Bibliografa Adicional</h3>
          <button 
            onClick={onClose}
            className="text-gray-400 hover:text-white transition-colors"
          >
            <X className="h-6 w-6" />
          </button>
        </div>
        
        <div className="p-6 overflow-y-auto flex-1">
          {loading ? (
            <div className="flex flex-col items-center justify-center py-10">
              <p className="text-gray-400">Cargando material bibliogrfico...</p>
            </div>
          ) : error ? (
            <div className="flex flex-col items-center justify-center py-10">
              <p className="text-red-400">{error}</p>
            </div>
          ) : capsulas.length === 0 ? (
            <div className="flex flex-col items-center justify-center py-10">
              <BookOpen className="h-16 w-16 text-gray-600 mb-4" />
              <p className="text-gray-400 text-center">No hay material bibliogrfico adicional disponible.</p>
            </div>
          ) : (
            <div className="space-y-6">
              {capsulas.map((capsula) => (
                <div key={capsula.id} className="bg-gray-800/70 rounded-lg overflow-hidden border border-gray-700/50">
                  <div className="p-5">
                    <h4 className="text-lg font-medium text-white mb-2">{capsula.titulo}</h4>
                    <p className="text-gray-300 text-sm mb-4">{capsula.descripcion}</p>
                    
                    <div className="flex justify-end">
                      <a 
                        href={capsula.link_drive} 
                        target="_blank" 
                        rel="noopener noreferrer"
                        className="px-4 py-2 bg-blue-600/30 hover:bg-blue-600/40 text-blue-300 rounded-lg transition-colors flex items-center space-x-2"
                      >
                        <ExternalLink className="h-4 w-4" />
                        <span>Ver material</span>
                      </a>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
        
        <div className="p-4 border-t border-gray-700 bg-gray-900/80">
          <button
            onClick={onClose}
            className="w-full py-2 bg-gray-700 hover:bg-gray-600 text-white rounded-lg transition-colors"
          >
            Cerrar
          </button>
        </div>
      </div>
    </div>
  );
};

const CourseContent: React.FC = () => {
  const [userData, setUserData] = useState<UserData>({});
  const [course, setCourse] = useState<Course | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  
  // Estado para el modal de materiales
  const [isModalOpen, setIsModalOpen] = useState<boolean>(false);
  const [currentMaterials, setCurrentMaterials] = useState<Material[]>([]);
  const [currentLessonTitle, setCurrentLessonTitle] = useState<string>('');
  
  // Estado para el modal de cpsulas
  const [capsulas, setCapsulas] = useState<Capsula[]>([]);
  const [isCapsulasModalOpen, setIsCapsulasModalOpen] = useState<boolean>(false);
  const [loadingCapsulas, setLoadingCapsulas] = useState<boolean>(false);
  const [capsulasError, setCapsulasError] = useState<string | null>(null);
  
  // Estado para los dropdowns de mdulos
  const [openModules, setOpenModules] = useState<{ [key: number]: boolean }>({});

  // Estados calculados para informacin del curso
  const [totalDuration, setTotalDuration] = useState<string>("0h");
  const [nextClass, setNextClass] = useState<string>("No hay clases prximamente");
  const [progress, setProgress] = useState<string>("0%");
  
  // Estado para tracking de clases completadas (para la nueva seccin de estadsticas)
  const [completedLessons, setCompletedLessons] = useState<string>("0/0");
  const [totalMaterials, setTotalMaterials] = useState<number>(0);
  const [remainingHours, setRemainingHours] = useState<string>("0h 0min");

  // Manejador de cierre de sesin para el dropdown
  const handleLogout = () => {
    localStorage.removeItem('userToken');
    localStorage.removeItem('userData');
    window.location.href = '/login';
  };

  // Funcin para alternar el estado de desplegado de un mdulo
  const toggleModule = (moduleId: number) => {
    setOpenModules(prev => ({
      ...prev,
      [moduleId]: !prev[moduleId]
    }));
  };

  // Funcin para calcular la duracin total del curso
  const calculateTotalDuration = (modules: Module[]) => {
    let totalMinutes = 0;
    
    modules.forEach(module => {
      module.lessons.forEach(lesson => {
        const durationStr = lesson.duration;
        
        // Extraer horas y minutos de los formatos como "1h 30min" o "45min"
        const hoursMatch = durationStr.match(/(\d+)h/);
        const minutesMatch = durationStr.match(/(\d+)min/);
        
        const hours = hoursMatch ? parseInt(hoursMatch[1]) : 0;
        const minutes = minutesMatch ? parseInt(minutesMatch[1]) : 0;
        
        totalMinutes += (hours * 60) + minutes;
      });
    });
    
    // Convertir de nuevo a formato legible
    const hours = Math.floor(totalMinutes / 60);
    const mins = totalMinutes % 60;
    
    return hours > 0 
      ? mins > 0 ? `${hours}h ${mins}min` : `${hours}h` 
      : `${mins}min`;
  };

  // Funcin para encontrar la prxima clase
  const findNextClass = (modules: Module[]) => {
    const today = new Date();
    let nextClassDate: Date | null = null;
    let nextClassInfo = "";
    
    modules.forEach(module => {
      if (module.locked) return;
      
      module.lessons.forEach(lesson => {
        if (lesson.locked) return;
        
        // Si hay una fecha en formato raw, usarla para comparacin
        if (lesson.date_raw) {
          const lessonDate = new Date(lesson.date_raw);
          
          // Si la fecha de la clase es futura y es anterior a la prxima clase encontrada
          // o no se ha encontrado ninguna prxima clase an
          if (lessonDate > today && (!nextClassDate || lessonDate < nextClassDate)) {
            nextClassDate = lessonDate;
            nextClassInfo = `${lesson.date} - ${lesson.title}`;
          }
        }
      });
    });
    
    return nextClassInfo || "No hay clases prximamente";
  };

  // Funcin para calcular el progreso total del curso
  const calculateProgress = (modules: Module[]) => {
    let totalLessons = 0;
    let completedLessons = 0;
    
    modules.forEach(module => {
      module.lessons.forEach(lesson => {
        totalLessons++;
        if (lesson.completed) {
          completedLessons++;
        }
      });
    });
    
    if (totalLessons === 0) return "0%";
    
    const progressPercentage = Math.round((completedLessons / totalLessons) * 100);
    return `${progressPercentage}%`;
  };
  
  // Calcular el nmero total de materiales
  const calculateTotalMaterials = (modules: Module[]) => {
    let count = 0;
    modules.forEach(module => {
      module.lessons.forEach(lesson => {
        if (lesson.materiales) {
          count += lesson.materiales.length;
        }
      });
    });
    return count;
  };
  
  // Calcular las horas restantes
  const calculateRemainingHours = (modules: Module[]) => {
    let totalMinutes = 0;
    
    modules.forEach(module => {
      module.lessons.forEach(lesson => {
        if (!lesson.completed) {
          const durationStr = lesson.duration;
          const hoursMatch = durationStr.match(/(\d+)h/);
          const minutesMatch = durationStr.match(/(\d+)min/);
          
          const hours = hoursMatch ? parseInt(hoursMatch[1]) : 0;
          const minutes = minutesMatch ? parseInt(minutesMatch[1]) : 0;
          
          totalMinutes += (hours * 60) + minutes;
        }
      });
    });
    
    const hours = Math.floor(totalMinutes / 60);
    const mins = totalMinutes % 60;
    
    return hours > 0 
      ? mins > 0 ? `${hours}h ${mins}min` : `${hours}h` 
      : `${mins}min`;
  };
  
  // Calcular completedLessons como fraccin
  const calculateCompletedLessons = (modules: Module[]) => {
    let total = 0;
    let completed = 0;
    
    modules.forEach(module => {
      module.lessons.forEach(lesson => {
        total++;
        if (lesson.completed) {
          completed++;
        }
      });
    });
    
    return `${completed}/${total}`;
  };

  // Funcin para cargar las cpsulas bibliogrficas
  const fetchCapsulas = async (): Promise<void> => {
    try {
      setLoadingCapsulas(true);
      setCapsulasError(null);
      
      // Usar la ruta con el prefijo /api actualizado
      const response = await api.get<Capsula[]>(`/api/custom-capsulas`);
      
      const capsulasData: Capsula[] = response.data || [];
      setCapsulas(capsulasData);
      setLoadingCapsulas(false);
    } catch (error: any) {
      
      const errorMessage: string = error.response?.status === 404
        ? 'La ruta de cpsulas no est disponible. Verifica la configuracin de Strapi.'
        : 'No se pudieron cargar los materiales bibliogrficos adicionales.';
        
      setCapsulasError(errorMessage);
      setCapsulas([]);
      setLoadingCapsulas(false);
    }
  };

  // Funcin para actualizar el progreso de una unidad/leccin
  const updateLessonProgress = async (lessonId: number, completed: boolean) => {
    try {
      // Actualizado para usar la ruta correcta con prefijo /api
      await api.put(`/api/cursos/update-progress/${lessonId}`, {
        completada: completed
      });
      
      // Actualizar la UI sin necesidad de recargar todo el curso
      setCourse(prevCourse => {
        if (!prevCourse) return null;
        
        const updatedModules = prevCourse.modules.map(module => {
          const updatedLessons = module.lessons.map(lesson => {
            if (lesson.id === lessonId) {
              return { ...lesson, completed };
            }
            return lesson;
          });
          
          // Recalcular el progreso del mdulo
          const totalUnidades: number = updatedLessons.length;
          const unidadesCompletadas: number = updatedLessons.filter(l => l.completed).length;
          const progress: number = totalUnidades > 0 ? Math.round((unidadesCompletadas / totalUnidades) * 100) : 0;
          const completed: boolean = progress === 100;
                    
          return {
            ...module,
            lessons: updatedLessons,
            progress,
            completed
          };
        });
        
        return { ...prevCourse, modules: updatedModules };
      });
      
      if (course) {
        const updatedModules = course.modules.map(module => {
          const updatedLessons = module.lessons.map(lesson => {
            if (lesson.id === lessonId) {
              return { ...lesson, completed };
            }
            return lesson;
          });
          return { ...module, lessons: updatedLessons };
        });
        
        setProgress(calculateProgress(updatedModules));
        setCompletedLessons(calculateCompletedLessons(updatedModules));
        setRemainingHours(calculateRemainingHours(updatedModules));
      }
      
    } catch (error) {
      // Opcionalmente mostrar un mensaje de error al usuario
    }
  };

  // Funcin para marcar una leccin como completada
  const markLessonAsCompleted = (lessonId: number) => {
    updateLessonProgress(lessonId, true);
  };

  // Funcin para marcar una leccin como no completada
  const markLessonAsNotCompleted = (lessonId: number) => {
    updateLessonProgress(lessonId, false);
  };

  // Funcin para cargar el curso - Actualizada para usar 'my-courses'
  const fetchCourse = async () => {
    try {
      setLoading(true);
      setError(null);
      
      // Usar la ruta /api/cursos/my-courses para obtener el curso del usuario
      const response = await api.get('/api/cursos/my-courses');
      
      // Verificar si los datos son un array o un objeto
      let courseData: Course;
      if (Array.isArray(response.data)) {
        // Si es un array, tomar el primer curso (si existe)
        if (response.data.length === 0) {
          setError('No tienes cursos asignados.');
          setLoading(false);
          return;
        }
        courseData = response.data[0];
      } else {
        // Si es un objeto, usarlo directamente
        courseData = response.data;
      }
      
      // Asegurarse de que los datos tienen el formato correcto
      if (!courseData) {
        setError('No se recibieron datos del curso');
        setLoading(false);
        return;
      }
  
      // Normalizar estructura de datos (backend usa modulos/unidades, frontend modules/lessons)
      if (courseData.modulos && !courseData.modules) {
        courseData.modules = courseData.modulos.map((modulo: any) => ({
          ...modulo,
          title: modulo.titulo,
          description: modulo.descripcion,
          duration: modulo.duracion,
          lessons: modulo.unidades?.map((unidad: any) => ({
            ...unidad,
            title: unidad.titulo,
            description: unidad.descripcion,
            duration: unidad.duracion,
            date: unidad.fecha_clase,
            date_raw: unidad.fecha_clase,
            completed: unidad.completada,
            locked: false, // Asumiendo que no hay propiedad locked en el backend
            hasResources: unidad.materiales?.length > 0,
            materiales: unidad.materiales || [],
            link_zoom: unidad.link_zoom,
            type: 'video' // Asumiendo que todas son videos
          })) || []
        }));
      }
  
      if (!courseData.modules) {
        setError('El formato de datos del curso no es vlido (falta propiedad modules/modulos).');
        setLoading(false);
        return;
      }
      
      // Calcular valores adicionales
      const duration = calculateTotalDuration(courseData.modules);
      const nextClassInfo = findNextClass(courseData.modules);
      const progressInfo = calculateProgress(courseData.modules);
      const materialsCount = calculateTotalMaterials(courseData.modules);
      const remaining = calculateRemainingHours(courseData.modules);
      const lessonsCount = calculateCompletedLessons(courseData.modules);
      
      setCourse(courseData);
      setTotalDuration(duration);
      setNextClass(nextClassInfo);
      setProgress(progressInfo);
      setTotalMaterials(materialsCount);
      setRemainingHours(remaining);
      setCompletedLessons(lessonsCount);
      
      // Inicializar estados de mdulos (cules estn abiertos)
      const initialOpenState: { [key: number]: boolean } = {};
      courseData.modules.forEach((module: Module, index: number) => {
        // Abrir por defecto solo el primer mdulo que no est completado
        const isFirstIncomplete = !module.completed && 
          !courseData.modules.slice(0, index).some((m: Module) => !m.completed);
        initialOpenState[module.id] = isFirstIncomplete;
      });
      setOpenModules(initialOpenState);
      
      setLoading(false);
    } catch (err: any) {
      
      // Manejar diferentes tipos de errores
      if (err.response?.status === 403) {
        setError('No tienes acceso a este curso.');
      } else if (err.response?.status === 404) {
        setError('Curso no encontrado.');
      } else if (err.response?.status === 401) {
        setError('Sesin expirada. Por favor, inicia sesin nuevamente.');
        // Redirigir a login
        setTimeout(() => {
          handleLogout();
        }, 2000);
      } else {
        setError('No se pudo cargar el contenido del curso. Por favor, intenta de nuevo.');
      }
      
      setLoading(false);
    }
  };
  
  useEffect(() => {
    // Cargar datos del usuario desde localStorage
    try {
      const userDataString = localStorage.getItem('userData');
      if (userDataString) {
        const parsedUserData = JSON.parse(userDataString);
        setUserData(parsedUserData);
      }
    } catch (error) {
    }
    
    // Cargar datos del curso
    fetchCourse();
    
    // Cargar las cpsulas
    fetchCapsulas();
  }, []); // Corregido el cierre del array de dependencias

  // Funcin para abrir el modal de materiales
  const openMaterialsModal = (lesson: Lesson) => {
    if (lesson.materiales && lesson.materiales.length > 0) {
      setCurrentMaterials(lesson.materiales);
      setCurrentLessonTitle(lesson.title);
      setIsModalOpen(true);
    }
  };
  
  // Funciones para abrir y cerrar el modal de cpsulas
  const openCapsulasModal = () => {
    setIsCapsulasModalOpen(true);
  };

  const closeCapsulasModal = () => {
    setIsCapsulasModalOpen(false);
  };

  const getUserName = () => {
    if (userData.nombre && userData.apellido) {
      return `${userData.nombre} ${userData.apellido}`;
    } else if (userData.nombre) {
      return userData.nombre;
    } else if (userData.email) {
      return userData.email.split('@')[0];
    } else {
      return "Usuario";
    }
  };

  // Renderizado condicional segn el estado
  if (loading) {
    return (
      <div className="flex justify-center items-center min-h-screen bg-gradient-to-br from-[#040b18] via-[#071223] to-[#091632]">
        <div className="text-white">Cargando contenido del curso...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex justify-center items-center min-h-screen bg-gradient-to-br from-[#040b18] via-[#071223] to-[#091632]">
        <div className="text-red-400 p-6 bg-gray-800/70 rounded-lg font-medium">{error}</div>
      </div>
    );
  }

  if (!course) {
    return (
      <div className="flex justify-center items-center min-h-screen bg-gradient-to-br from-[#040b18] via-[#071223] to-[#091632]">
        <div className="text-white p-6 bg-gray-800/70 rounded-lg">
          No se encontr el curso solicitado.
        </div>
      </div>
    );
  }
// Renderizado principal
return (
  <div className="min-h-screen bg-gradient-to-br from-[#040b18] via-[#071223] to-[#091632]">
    {/* Bienvenida */}
    <div className="max-w-7xl mx-auto px-6 py-8">
      <h1 className="text-3xl font-bold text-white mb-2">
        Bienvenido, {getUserName()}! 
      </h1>
      <p className="text-blue-400">
        Contina tu viaje de aprendizaje en {course.titulo}
      </p>
    </div>

    <div className="max-w-7xl mx-auto px-6 pb-12">
      {/* Dashboard Stats - Con mejores contrastes */}
      <div className="mb-12">
        {/* Contenedor principal con contraste mejorado y centrado */}
        <div className="bg-[#0d1526] backdrop-blur-lg rounded-2xl border border-gray-800 overflow-hidden shadow-xl shadow-blue-900/10">
          {/* Fila superior con las estadsticas principales - centrado con solo 3 tarjetas */}
          <div className="grid grid-cols-1 md:grid-cols-3 divide-y md:divide-y-0 md:divide-x divide-gray-800">
            {/* Tarjeta 1: Progreso Total - Con mejor contraste */}
            <div className="p-6 hover:bg-blue-900/10 transition-all duration-300 group relative overflow-hidden">
              {/* Efecto de destello en hover */}
              <div className="absolute -inset-1 bg-blue-500/10 blur-md opacity-0 group-hover:opacity-100 transition-opacity duration-700 rounded-full"></div>
              
              <div className="relative">
                {/* Header con icono y valor */}
                <div className="flex items-center justify-between mb-4">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 rounded-lg bg-blue-600/20 flex items-center justify-center">
                      <BookOpen className="h-5 w-5 text-blue-400" />
                    </div>
                    <span className="text-sm text-gray-300 font-medium uppercase tracking-wider">Progreso</span>
                  </div>
                  <span className="text-2xl font-bold text-white">{progress}</span>
                </div>
                
                {/* Barra de progreso con mejor contraste */}
                <div className="mt-2">
                  <div className="relative h-2 bg-gray-800 rounded-full overflow-hidden">
                    {/* Barra de progreso actual */}
                    <div 
                      className="h-full bg-gradient-to-r from-blue-600 via-blue-500 to-blue-400 rounded-full transition-all duration-1000"
                      style={{ width: progress }}
                    ></div>
                  </div>
                </div>
                
                {/* Footer con mensaje motivacional */}
                <p className="text-xs text-blue-400 mt-3 font-medium">
                  {progress === "100%" ? "Felicitaciones!" : "Contina aprendiendo"}
                </p>
              </div>
            </div>
            
            {/* Tarjeta 2: Duracin Total - Con mejor contraste */}
            <div className="p-6 hover:bg-purple-900/10 transition-all duration-300 group relative overflow-hidden">
              {/* Efecto de destello en hover */}
              <div className="absolute -inset-1 bg-purple-500/10 blur-md opacity-0 group-hover:opacity-100 transition-opacity duration-700 rounded-full"></div>
              
              <div className="relative">
                {/* Header con icono */}
                <div className="flex items-center justify-between mb-4">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 rounded-lg bg-purple-600/30 flex items-center justify-center">
                      <Clock className="h-5 w-5 text-white" />
                    </div>
                    <span className="text-sm text-white font-medium uppercase tracking-wider">Duracin</span>
                  </div>
                  <div className="text-2xl font-bold text-white">{totalDuration}</div>
                </div>
                
                {/* Visualizacin grfica de la duracin con mejor contraste */}
                <div className="mt-2 flex items-center gap-1.5">
                  {[...Array(5)].map((_, i) => {
                    // Extraer nmero de horas para determinar el llenado
                    const hourMatch = totalDuration.match(/(\d+)h/);
                    const hours = hourMatch ? parseInt(hourMatch[1]) : 0;
                    
                    return (
                      <div 
                        key={i} 
                        className={`h-8 flex-1 rounded-md ${
                          // Diferentes tonos de morado con mejor contraste
                          i < Math.ceil(hours / 4) 
                            ? `bg-gradient-to-b from-purple-500/80 to-purple-600/60` 
                            : `bg-gray-800`
                        }`}
                      ></div>
                    );
                  })}
                </div>
              </div>
            </div>
            
            {/* Tarjeta 3: Prxima Clase - Con mejor contraste */}
            <div className="p-6 hover:bg-green-900/10 transition-all duration-300 group relative overflow-hidden">
              {/* Efecto de destello en hover */}
              <div className="absolute -inset-1 bg-green-500/10 blur-md opacity-0 group-hover:opacity-100 transition-opacity duration-700 rounded-full"></div>
              
              <div className="relative">
                {/* Header con icono */}
                <div className="flex items-center gap-3 mb-4">
                  <div className="w-10 h-10 rounded-lg bg-green-600/20 flex items-center justify-center">
                    <Calendar className="h-5 w-5 text-green-400" />
                  </div>
                  <span className="text-sm text-gray-300 font-medium uppercase tracking-wider">Prxima Clase</span>
                </div>
                
                {/* Informacin de la prxima clase con mejor formato y contraste */}
                <div className="mt-2 bg-gray-800 rounded-xl p-3 border border-green-600/10">
                  {nextClass === "No hay clases prximamente" ? (
                    <p className="text-gray-400 text-sm">No hay clases programadas</p>
                  ) : (
                    <>
                      <p className="text-white font-medium mb-1">
                        {nextClass.split('-')[0]?.trim()}
                      </p>
                      <p className="text-green-400 text-sm">
                        {nextClass.split('-')[1]?.trim() || ''}
                      </p>
                    </>
                  )}
                </div>
              </div>
            </div>
          </div>

          {/* Seccin inferior con estadsticas adicionales - mejor contraste */}
          <div className="border-t border-gray-800 p-6 bg-gray-900/30">
            <div className="grid grid-cols-1 sm:grid-cols-3 gap-6">
              {/* Estadstica 1: Clases Completadas */}
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                  <Video className="h-5 w-5 text-blue-400" />
                  <span className="text-gray-300">Clases completadas</span>
                </div>
                <span className="text-white font-semibold">{completedLessons}</span>
              </div>
              
              {/* Estadstica 2: Materiales Disponibles */}
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                  <FileText className="h-5 w-5 text-white" />
                  <span className="text-gray-300">Materiales disponibles</span>
                </div>
                <span className="text-white font-semibold">{totalMaterials}</span>
              </div>
              
              {/* Estadstica 3: Horas Restantes */}
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                  <Clock className="h-5 w-5 text-green-400" />
                  <span className="text-gray-300">Horas restantes</span>
                </div>
                <span className="text-white font-semibold">{remainingHours}</span>
              </div>
            </div>
          </div>
        </div>
        
        {/* Indicador de actualizacin */}
        <div className="mt-2 text-right">
          <span className="text-xs text-gray-400 flex items-center justify-end gap-1">
            <Clock className="h-3 w-3" />
            Actualizado hace 2 horas
          </span>
        </div>
      </div>

      {/* Mdulos - Con sistema de dropdown y mejor contraste */}
      <div className="space-y-4">
        {course.modules.map((module) => (
          <div key={module.id} className="bg-[#0d1526] backdrop-blur-sm border border-gray-800 rounded-2xl overflow-hidden">
            {/* Cabecera del Mdulo (siempre visible) */}
            <div 
              className="p-6 border-b border-gray-800 cursor-pointer"
              onClick={() => toggleModule(module.id)}
            >
              <div className="flex items-start justify-between">
                <div className="flex-1">
                  <div className="flex items-center space-x-3">
                    <span className="px-3 py-1 bg-blue-600/20 text-blue-400 text-sm rounded-full">
                      Mdulo {module.moduleNumber || module.id}
                    </span>
                    <span className={`px-3 py-1 rounded-full text-sm ${
                      module.locked 
                        ? 'bg-gray-700/50 text-gray-400'
                        : module.completed 
                          ? 'bg-green-600/20 text-green-400' 
                          : 'bg-blue-600/20 text-blue-400'
                    }`}>
                      {module.locked 
                        ? 'Bloqueado'
                        : module.completed 
                          ? 'Completado' 
                          : 'En progreso'}
                    </span>
                  </div>
                  <div className="flex items-start justify-between mt-3">
                    <h2 className="text-xl font-semibold text-white">
                      {module.title}
                    </h2>
                    <button className="text-gray-400 hover:text-white transition-colors">
                      {openModules[module.id] ? (
                        <ChevronUp className="h-5 w-5" />
                      ) : (
                        <ChevronDown className="h-5 w-5" />
                      )}
                    </button>
                  </div>
                  <p className="text-gray-300 mt-2 pr-8">{module.description}</p>
                  
                  {/* Barra de progreso con mejor contraste */}
                  <div className="mt-4">
                    <div className="flex justify-between text-sm mb-2">
                      <span className="text-gray-300">Progreso del mdulo</span>
                      <span className="text-blue-400">{module.progress}%</span>
                    </div>
                    <div className="h-2 bg-gray-800 rounded-full">
                      <div 
                        className="h-full bg-gradient-to-r from-blue-600 to-blue-500 rounded-full transition-all"
                        style={{ width: `${module.progress}%` }}
                      ></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/* Contenido del mdulo (visible solo si est desplegado) */}
            {openModules[module.id] && (
              <>
                {/* Lista de Lecciones */}
                <div className="divide-y divide-gray-800">
                  {module.lessons.map((lesson, index) => (
                    <div 
                      key={index}
                      className="p-6 hover:bg-gray-800/30 transition-colors"
                    >
                      <div className="flex items-start justify-between">
                        <div className="flex items-start space-x-4">
                          <div className={`w-10 h-10 rounded-xl flex items-center justify-center ${
                            lesson.locked 
                              ? 'bg-gray-800'
                              : lesson.completed 
                                ? 'bg-green-600/20' 
                                : 'bg-blue-600/20'
                          }`}>
                            {lesson.locked ? (
                              <Lock className="h-5 w-5 text-gray-500" />
                            ) : lesson.completed ? (
                              <span title="Marcar como no completado">
                                <CheckCircle2 
                                  className="h-5 w-5 text-green-400 cursor-pointer" 
                                  onClick={(e) => {
                                    e.stopPropagation();
                                    markLessonAsNotCompleted(lesson.id);
                                  }}
                                />
                              </span>
                            ) : (
                              <span title="Marcar como completado">
                                <PlayCircle 
                                  className="h-5 w-5 text-blue-400 cursor-pointer" 
                                  onClick={(e) => {
                                    e.stopPropagation();
                                    markLessonAsCompleted(lesson.id);
                                  }}
                                />
                              </span>
                            )}
                          </div>
                          <div>
                            <h3 className={`font-medium ${lesson.locked ? 'text-gray-500' : 'text-white'}`}>
                              {lesson.title}
                            </h3>
                            {lesson.description && (
                              <p className={`text-sm mt-1 ${lesson.locked ? 'text-gray-600' : 'text-gray-300'}`}>
                                {lesson.description}
                              </p>
                            )}
                            <div className="flex flex-wrap items-center gap-4 mt-3">
                              <span className="flex items-center text-sm text-gray-400">
                                <Clock className="h-4 w-4 mr-1" />
                                {lesson.duration}
                              </span>
                              {lesson.date && (
                                <span className="flex items-center text-sm text-gray-400">
                                  <Calendar className="h-4 w-4 mr-1" />
                                  {lesson.date}
                                </span>
                              )}
                            </div>
                          </div>
                        </div>

                        {/* Acciones de la leccin - Simplificadas y con mejor contraste */}
                        <div className="flex items-center space-x-3">
                          {lesson.hasResources && !lesson.locked && (
                            <button 
                              className="p-3 text-gray-300 hover:text-white transition-colors bg-gray-800/70 hover:bg-gray-700/70 rounded-lg"
                              title="Ver materiales de la clase"
                              onClick={(e) => {
                                e.stopPropagation();
                                openMaterialsModal(lesson);
                              }}
                            >
                              <FileText className="h-5 w-5" />
                            </button>
                          )}
                          {lesson.link_zoom && !lesson.locked && (
                            <a 
                              href={lesson.link_zoom}
                              target="_blank" 
                              rel="noopener noreferrer"
                              className="p-3 text-blue-300 hover:text-blue-200 transition-colors bg-blue-600/20 hover:bg-blue-600/30 rounded-lg"
                              title="Unirse a la clase por Zoom"
                              onClick={(e) => e.stopPropagation()}
                            >
                              <Video className="h-5 w-5" />
                            </a>
                          )}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>

                {/* Footer del Mdulo - Solo para mdulos bloqueados */}
                {module.locked && (
                  <div className="p-6 bg-gray-900/50 border-t border-gray-800">
                    <div className="flex items-center justify-between">
                      <p className="text-gray-300 text-sm">
                        Complete el mdulo anterior para desbloquear este contenido
                      </p>
                      <Lock className="h-5 w-5 text-gray-500" />
                    </div>
                  </div>
                )}
              </>
            )}
          </div>
        ))}
      </div>

      {/* Botn para bibliografa adicional */}
      <div className="mt-12 bg-gradient-to-r from-purple-600/10 via-indigo-600/10 to-blue-600/10 p-8 rounded-2xl border border-gray-800/50 text-center">
        <BookOpen className="h-12 w-12 text-indigo-400 mx-auto mb-4" />
        <h3 className="text-xl font-semibold text-white mb-2">Material Bibliogrfico Adicional</h3>
        <p className="text-gray-300 mb-6 max-w-2xl mx-auto">
          Accede a lecturas complementarias, artculos y recursos adicionales para profundizar en los conceptos del curso.
        </p>
        <button
          onClick={openCapsulasModal}
          className="px-6 py-3 bg-indigo-600/30 hover:bg-indigo-600/40 text-indigo-300 rounded-xl transition-all duration-300 flex items-center gap-2 mx-auto border border-indigo-500/30"
        >
          <BookOpen className="h-5 w-5" />
          <span className="font-medium">Ver bibliografa adicional</span>
        </button>
        
        {loadingCapsulas && (
          <p className="text-gray-400 text-sm mt-4">Cargando datos bibliogrficos...</p>
        )}
        
        {capsulasError && (
          <p className="text-red-400 text-sm mt-4">{capsulasError}</p>
        )}
      </div>

      {/* Soporte Tcnico con mejor contraste */}
      <div className="mt-12 relative">
        <div className="absolute -inset-1 bg-gradient-to-r from-blue-600/10 to-purple-600/10 rounded-xl blur-xl" />
        <div className="relative bg-gray-900/50 backdrop-blur-xl border border-gray-700 rounded-xl p-6 text-center">
          <p className="text-gray-200 font-medium mb-4">
            Necesitas ayuda?
          </p>
          <a 
            href="/help" 
            className="inline-flex items-center gap-2 px-5 py-2.5 rounded-lg
                       bg-blue-600/20 hover:bg-blue-600/30
                       text-blue-300 text-sm font-medium
                       border border-blue-600/30
                       transition-all duration-300"
          >
            <MessageSquare className="w-4 h-4" />
            <span>Centro de Ayuda</span>
          </a>
        </div>
      </div>
    </div>

    {/* Modal de materiales */}
    <MaterialModal 
      isOpen={isModalOpen}
      onClose={() => setIsModalOpen(false)}
      materials={currentMaterials}
      lessonTitle={currentLessonTitle}
    />
    
    {/* Modal de cpsulas bibliogrficas */}
    <CapsulasModal 
      isOpen={isCapsulasModalOpen}
      onClose={closeCapsulasModal}
      capsulas={capsulas}
      loading={loadingCapsulas}
      error={capsulasError}
    />
  </div>
);
};

export default CourseContent;